<template>
  <div class="blog">
    <div class="video"></div>
    <img class="imgpipi" src="../../assets/pipi.jpg" alt />
    <p class="name"></p>
    <div class="container">
      <!-- Example row of columns -->
      <div class="row">
        <div class="col-md-4">
          <router-link to="/tensorflow">
            <div class="col-md-12">
              <h5>JavaScript</h5>
              <p>webweb</p>
              <p>
                <span class="btn btn-default"></span>
                <a class="btn btn-default" href="#" role="button">View details</a>
              </p>
            </div>
          </router-link>
        </div>
      </div>
      <hr />

      <footer>
        <p>@ pipi</p>
      </footer>
    </div>
  </div>
</template>
<script>
export default {
  name: "blog",
  data() {
    return {};
  }
};
</script>
<style lang="less" scoped>
.blog {
  background-color: rgba(255, 255, 255, 0.1);
  padding-top: 1px;
  padding-bottom: 1px;
  width: 100vw;
  overflow: hidden;
  min-height: 100vh;
}
.video {
  width: 100vw;
  height: 100vh;
  background-image: url(../../assets/preview.jpg);
  background-position: center;
  background-size: cover;
  z-index: -1;
  position: fixed;
  top: 0;
}
.imgpipi {
  width: 100px;
  border-radius: 50px;
  display: block;
  margin: 50px auto 10px;
}
.name {
  text-align: center;
  font-size: 30px;
  color: #fff;
  margin-bottom: 40px;
}
.container {
  padding: 0;
}
.row .col-md-4 .col-md-12 {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  color: #060607;
}
.row .col-md-4 .col-md-12:hover {
  background-color: rgba(255, 255, 255, 0.4);
}
.row .col-md-4 .col-md-12 h5{
  padding: 5px 0;
}
@media (min-width: 320px) and (max-width: 600px) {
  .row .col-md-4 .col-md-12 {
    width: 90vw;
    margin: 0 auto;
  }
}
</style>


